import { graphql } from "gatsby"
import React from "react";
import {MainSectionWithPhotosContext} from "../layout";
import CommonHead from "../layout/components/Head";
import GooglePhotosAlbum from "../layout/components/GooglePhotosAlbum";

const PageTemplate = (props) => {
  const photosLink = props.data.page.photosLink;
  return (
    <MainSectionWithPhotosContext {...props}>
      <GooglePhotosAlbum 
        gap={0} 
        albumTitle={props.data.page.name} 
        photos={props.data.photos.photos}
        photosLink={photosLink}
      />
    </MainSectionWithPhotosContext>
  )
};

export function Head(props) {
  return <CommonHead {...props} />;
}

export const query = graphql`
  query Page($id: String!, $albumTitle: String!) {
    page: googleDocs(path: {eq: $id}) {
      name
      menu
      childMarkdownRemark {
        htmlAst
      }
      description
      modifiedTime

      showTitle
      photosLink
    }

    photos: googlePhotosAlbum(title: {eq: $albumTitle}) {
      photos {
        id
        file {
          childImageSharp {
            gatsbyImageData(
              width: 1000
            )
          }
        }
      }
    }
  }
`;

export default PageTemplate;