import React from "react";
import {GatsbyImage, getImage} from "gatsby-plugin-image";
import {H1} from "../Header";
import PhotoBoard from "../PhotoBoard";
import ThemedLink from "../Link";

import "./styles.scss";

export function GooglePhotosAlbum(props) {
    const {albumTitle, photosLink} = props;
    return (
        <div className="GooglePhotosBoards">
            <div className="GooglePhotosAlbumTitle">
                <H1>{albumTitle}</H1>
                <p>Add your photos to <ThemedLink href={photosLink}>this album</ThemedLink></p>
            </div>
            <PhotoBoard gap={props.gap}>
                {props.photos.map(photoNode => {
                    return (
                        <GatsbyImage
                            key={photoNode.id}
                            image={getImage(photoNode.file)}
                            alt={props.albumTitle}
                        />
                    );
                })}
            </PhotoBoard>
        </div>
    );
}

export default GooglePhotosAlbum;